import React, { useState, useEffect } from 'react';
import './themeSelector.css';

export const ThemeSelector = () => {
    const currentTheme = localStorage.getItem('theme');
    const [theme, setTheme] = useState((currentTheme === undefined) ? 'dark' : currentTheme);
    
    const colors = {
        dark: {
            bg: '#1a1d22',
            text: '#e8e8e8',
            box: '#22262b'
        },
        light: {
            bg: '#e8e8e8',
            text: '#1a1d22',
            box: '#d8d8d8'
        } 
    }

    const toogleTheme = () => {
        const root = document.documentElement;

        if(theme === 'light'){
            root.style.setProperty('--bg-color', colors.dark.bg);
            root.style.setProperty('--text-color', colors.dark.text);
            root.style.setProperty('--box-color', colors.dark.box);
            setTheme('dark');  
            localStorage.setItem('theme', 'dark');
        }
        else{
            root.style.setProperty('--bg-color', colors.light.bg);
            root.style.setProperty('--text-color', colors.light.text);
            root.style.setProperty('--box-color', colors.light.box);
            setTheme('light');
            localStorage.setItem('theme', 'light');
        }
    }

    useEffect(() => {
        if(currentTheme !== undefined){
            const root = document.documentElement;

            if(currentTheme === 'dark'){
                root.style.setProperty('--bg-color', colors.dark.bg);
                root.style.setProperty('--text-color', colors.dark.text);
                root.style.setProperty('--box-color', colors.dark.box);
                setTheme('dark');  
            }
            else{
                root.style.setProperty('--bg-color', colors.light.bg);
                root.style.setProperty('--text-color', colors.light.text);
                root.style.setProperty('--box-color', colors.light.box);
                setTheme('light');
            }
        }
        // eslint-disable-next-line
    }, []);
    
    return (
        <>
            {(theme === 'light') ?
                <button className="sun" onClick={toogleTheme}>
                    <i className="fas fa-sun"></i>
                    Light
                </button>
                :
                <button className="moon" onClick={toogleTheme}>
                    <i className="fas fa-moon"></i>
                    Dark
                </button>
            }
        </>
    )
}

