import React from 'react';

import bg from '../../img/presentation/bg.jpg';
import profile from '../../img/presentation/profile.jpg';
import './presentation.css';

export const Presentation = ( {languaje} ) => {

    return (
        <header id="presentation">
            <img src={bg} alt="Background" />
            <div className="cover firtsCover" />
            <div className="cover secondCover" />
            <div className="cover thirdCover" />
            <img className="profile" src={profile} alt="Profile" /> 
            {(languaje === 'es') &&
                <div className="presentation">
                    <h1>Bienvenido</h1>  
                    <h3>Mi nombre es Cuauhtli Fernando</h3>  
                    <h5>y soy Desarrollador de Software</h5>
                </div> 
            }
            {(languaje === 'pt') &&
                <div className="presentation">
                    <h1>Bem-vindo</h1>  
                    <h3>Meu nome é Cuauhtli Fernando</h3>  
                    <h5>e sou Desenvolvedor de Software</h5>
                </div> 
            }
            {(languaje === 'en') &&
                <div className="presentation">
                    <h1>Welcome</h1>  
                    <h3>My name is Cuauhtli Fernando</h3>  
                    <h5>and I'm Software Developer</h5>
                </div> 
            }
        </header> 
    )
}
