import React, { useEffect, useState } from 'react';
import { useLoadImages } from '../../hooks/useLoadImages';

import './project.css';

export const Project = ({project, setModal}) => {
    const [carrousel, setCarrousel] = useState(1);
    const [imgRoute, setImgRoute] = useState('default.jpg');
    const [switchImg, setSwitchImg] = useState(true);

    const images = useLoadImages(require.context('../../img/projects', false, /\.(png|jpe?g|svg)$/));
    const tools = useLoadImages(require.context('../../img/skills', false, /\.(png|jpe?g|svg)$/));
    const carrouselSpeed = 2500;

    /**
     * This effect handle the exit with the 'Esc' key
     */
    useEffect(() => {
        const handleEsc = (event) => {
            if (event.keyCode === 27) {
                document.getElementById('closeBtn').click();
            }
        };

        window.addEventListener('keydown', handleEsc);

        return () => {
            window.removeEventListener('keydown', handleEsc);
        };
    }, []);

    /**
     * This effect handle the carrousel img effect for every img with name projectName + number + extension
     * For example: If the project it's game, this will iterate over all img with the format game1.jpg, game2.jpg, etc...
     */
    useEffect(() => {
        let changeImg;
        if(project.img !== undefined){
            if(images[project.img + (carrousel + 1) + project.ext] !== undefined){
                setCarrousel(carrousel + 1);
                setImgRoute(project.img + carrousel + project.ext);
            }
            else{
                setCarrousel(1);
                setImgRoute(project.img + carrousel + project.ext);
            }
            changeImg = setTimeout(() => {setSwitchImg(!switchImg)}, carrouselSpeed);
        }
        
        return () => {
            clearTimeout(changeImg);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [switchImg]);

    return (
        <div className='fullCover'>
            <div className="blackScreen" onClick={setModal} />
            <div className="project">
                {
                    (imgRoute !== undefined) &&
                    <>
                        <img className="banner" src={images[imgRoute].default} alt="banner" />
                        <div className="bannerCover" />
                        <img className="bannerFull" src={images[imgRoute].default} alt="fullImg"/>
                    </>
                }
                <div className="projectInfo">
                    <button id="closeBtn" className="close" onClick={setModal}><i className="fas fa-times"></i></button>
                    <span className="projectName">{project.name}</span>
                    <span className="projectDescription">{project.description}</span>
                    <ul className="projectTools">
                        {
                            project.tools.map((tool) => {
                                return (
                                    <li key={"tool"+project.id+tool}>
                                        {
                                            (tools[tool+'.png'] !== undefined) 
                                            ? <img src={tools[tool+'.png'].default} alt={tool} title={tool}/> 
                                            : <>{tool.toUpperCase()}</>
                                        }
                                    </li>
                                )
                            })
                        }
                    </ul>
                    {
                        (project.note !== undefined) &&
                            <small>{project.note}</small>
                    }
                </div>
            </div>
        </div>
    )
}
