import React, { useState } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { isMobile } from 'react-device-detect';

import { Resume } from '../resume/Resume';
import { Studies } from '../studies/Studies';
import { Software } from '../software/Software';
import { Experience } from '../experience/Experience';
import { ThemeSelector } from '../themeSelector/ThemeSelector';

import './sections.css';
//import { LanguajeSelector } from '../languajeSelector/LanguajeSelector';

export const Sections = ( {languaje, handleLanguaje} ) => {
    const [menuMobile, setMenuMobile] = useState(isMobile);

    const toggleMenu = () => {
        setMenuMobile(!menuMobile);
    }

    const goTop = () => {
        let start = (window.screen.height / 100) * 90;
        window.scroll({ top: start, left: 0, behavior: 'smooth' });
        if(isMobile){
            toggleMenu();
        }
    }

    return (
        <Router>
            <div id="routerDiv">
                {
                    (menuMobile) ?
                        <button className="mobileToogleMenu" onClick={toggleMenu}><i className="fas fa-bars"></i></button>
                    :
                    <nav>
                        <ul>
                            <ThemeSelector />
                            {/* <LanguajeSelector languaje={ languaje } handleLanguaje={ handleLanguaje } /> */}
                            <li onClick={ goTop }>
                                <Link className="menuLink" to="/"><i className="fas fa-file-alt"></i> 
                                    {(languaje === 'es') && <>Resumen</>}
                                    {(languaje === 'en') && <>Summary</>}
                                    {(languaje === 'pt') && <>Resumo</>}
                                </Link>
                            </li>
                            <li onClick={ goTop }>
                                <Link className="menuLink" to="/studies"><i className="fas fa-graduation-cap"></i>
                                    {(languaje === 'es') && <>Estudios</>}
                                    {(languaje === 'en') && <>Studies</>}
                                    {(languaje === 'pt') && <>Estudos</>}
                                </Link>
                            </li>
                            <li onClick={ goTop }>
                                <Link className="menuLink" to="/experience"><i className="fas fa-briefcase"></i> 
                                    {(languaje === 'es') && <>Experiencia</>}
                                    {(languaje === 'en') && <>Experience</>}
                                    {(languaje === 'pt') && <>Experiência</>}
                                </Link>
                            </li>
                            <li onClick={ goTop }>
                                <Link className="menuLink" to="/projects"><i className="fas fa-project-diagram"></i> 
                                    {(languaje === 'es') && <>Proyectos</>}
                                    {(languaje === 'en') && <>Projects</>}
                                    {(languaje === 'pt') && <>Projetos</>}
                                </Link>
                            </li>
                            {
                                (isMobile) &&
                                <li onClick={ toggleMenu }>
                                    <span className="menuLink"><i className="fas fa-times"></i> 
                                        {(languaje === 'es') && <>Cerrar menú</>}
                                        {(languaje === 'en') && <>Close menu</>}
                                        {(languaje === 'pt') && <>Fechar menu</>}
                                    </span>
                                </li>
                            }
                        </ul>
                    </nav>
                }

                <div className="mainContainer">
                    <Switch>
                        <Route path="/experience">
                            <Experience />
                        </Route>
                        <Route path="/projects">
                            <Software />
                        </Route>
                        <Route path="/studies">
                            <Studies />
                        </Route>
                        <Route path="/">
                            <Resume languaje={languaje} />
                        </Route>
                    </Switch>
                </div> 

            </div>
        </Router>
    )
}