import React, { useState } from 'react';
import { useLoadImages } from '../../hooks/useLoadImages';

import dataEsp from '../../data/esp/studiesEsp.json';
import './studies.css';

export const Studies = () => {

    const [currentData, setCurrentData] = useState(dataEsp);

    const images = useLoadImages(require.context('../../img/badge', false, /\.(png|jpe?g|svg)$/));
    
    return (
        <section className="studiesSection">
            <h2> {currentData.title} </h2>  
            {
                currentData.content.map((item) => {
                    if(item.type === 'paragraph'){
                        return <p key={ "resume" + item.type + item.id }>{ item.info }</p>
                    }
                    else if(item.type === 'title'){
                        return <h4 key={ "title" + item.type + item.id }>{ item.info }</h4>
                    }
                    else if(item.type === 'school'){
                        return (
                            <div key={"schoolList" + item.id} className="container">
                                {
                                item.list.map((school) => {
                                    return (
                                        <div key={ "school" + school.id } className="school">
                                            <ul>
                                                <li> {school.carrer} </li>
                                                <li> {school.school} </li>
                                                <li> {school.lapse}  </li>
                                            </ul>
                                            <img src={school.image} alt={school.school}></img> 
                                        </div>
                                    )
                                })
                                }
                            </div>
                        )
                    }
                    else if(item.type === 'languajes'){
                        return(
                            <div key={"languajeContainer"+item.id} className="container">
                                {
                                    item.list.map((languaje) => {
                                        return (
                                            <div key={"languaje"+languaje.id} className="languaje">
                                                <img src={languaje.image} alt={languaje.languaje}></img>

                                                <span className="name">
                                                    <b>{languaje.languaje} </b><br />
                                                    {languaje.domain}
                                                </span> 
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        )
                    }
                    else if(item.type === 'certifications'){
                        return(
                            <div key={"certificationContainer"+item.id} className="container">
                                {
                                    item.list.map((certification) => {
                                        return(
                                            <a className="certification" href={certification.link} key={"certification"+certification.id} target="_blank" rel="noopener noreferrer">
                                                <img src={images[certification.badge]?.default} alt="Certificate badge"/> 
                                                <span className="name">{certification.name}</span>    
                                                <span className="date">{certification.date}</span>
                                                <i className="fas fa-external-link-alt"></i>
                                            </a>
                                        )
                                    })
                                }
                            </div>
                        )
                    }
                    else if(item.type === 'courses'){
                        return(
                            <div key={"courseContainer"+item.id} className="container">
                                {
                                    item.list.map((course) => {
                                        return(
                                            <div className="course" key={"course"+course.id}>
                                                <span className="name">{course.name}</span> 
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        )
                    }
                    return <></>
                })
            }
        </section>
    )
}
