import React, { useState } from 'react';
import { useLoadImages } from '../../hooks/useLoadImages';

import './software.css';
import dataEsp from '../../data/esp/softwareEsp.json';
import { Project } from '../project/Project';

export const Software = () => {
    const [currentData, setCurrentData] = useState(dataEsp);
    const [modalProject, setModalProject] = useState(false);
    const [project, setProject] = useState(null);

    const showModal = (visible, newProject) => {
        if(visible === true){ 
            setProject(newProject);
            setModalProject(visible);
        }
        else{
            setProject(null);
            setModalProject(visible);
        }
    }

    const images = useLoadImages(require.context('../../img/projects', false, /\.(png|jpe?g|svg)$/));

    return (
        <>
        <section className="softwareSection">
            <h2> {currentData.title} </h2>  
            {
                currentData.content.map((item) => {
                    if(item.type === 'paragraph'){
                        return <p key={ "resume" + item.type + item.id }>{ item.info }</p>
                    }
                    else if(item.type === 'title'){
                        return <h4 key={ "title" + item.type + item.id }>{ item.info }</h4>
                    }
                    else if(item.type === 'projects'){
                        return(
                            <ul key={"softwareList"+item.id} className="softwareList">
                            {
                                item.projects.map((project) => {
                                    return(
                                        <li className="project" key={"software"+item.id+"."+project.id} onClick={() => showModal(true, project)}>
                                            <img src={images[project.img + 1 + project.ext].default} alt={project.name}></img>
                                            <h3> {project.name} </h3>
                                            <button>Detalles</button>
                                        </li>
                                    )
                                })
                            }
                            </ul>
                        )
                    }
                    return <></>
                })
            }
        </section>
        {
            (modalProject) &&
                <Project project={project} setModal={ () => showModal(false, null) } />
        }
        </>
    )
}
