import React, { useState } from 'react';
import { useLoadImages } from '../../hooks/useLoadImages';

import './experience.css';
import dataEsp from '../../data/esp/experienceEsp.json';
import { Project } from '../project/Project';

export const Experience = () => {
    const [currentData, setCurrentData] = useState(dataEsp);
    const [modalProject, setModalProject] = useState(false);
    const [project, setProject] = useState(null);

    const showModal = (visible, newProject) => {
        if(visible === true){ 
            setProject(newProject);
            setModalProject(visible);
        }
        else{
            setProject(null);
            setModalProject(visible);
        }
    }

    const images = useLoadImages(require.context('../../img/work', false, /\.(png|jpe?g|svg)$/));

    return (
        <>
        <section className="experienceSection">
            <h2> {currentData.title} </h2>  
            {
                currentData.content.map((item) => {
                    if(item.type === 'paragraph'){
                        return <p key={ "resume" + item.type + item.id }>{ item.info }</p>
                    }
                    else if(item.type === 'title'){
                        return <h4 key={ "title" + item.type + item.id }>{ item.info }</h4>
                    }
                    else if(item.type === 'work'){
                        return (<div key={"work"+item.id} className="container work">
                                    <img className="logo" src={images[`${item.logo}`].default} alt={item.company} />
                                    <h4 className="position">{item.position}</h4>
                                    <h5 className="company">{item.company}
                                        <span className="dates">
                                            { item.startDate + " - " +item.endDate }
                                        </span>
                                    </h5>
                                    <h6>Roles desempeñados</h6>   
                                    <ul> 
                                    {
                                        item.activities.map((activity) => {
                                            return(<li key={"activity"+item.id+"."+activity.id}>{activity.name}</li>)
                                        })
                                    }
                                    </ul>
                                    <h6>Proyectos</h6>
                                    <ul className="projectList">
                                    {
                                        item.projects.map((project) => {
                                            return(
                                                <li className="project" key={"activity"+item.id+"."+project.id} onClick={() => showModal(true, project)}>
                                                    <i className="fas fa-list-alt"></i> {project.name}
                                                </li>
                                            )
                                        })
                                    }
                                    </ul>
                                </div>)
                    }
                    return <></>
                })
            }
        </section>
        {
            (modalProject) &&
                <Project project={project} setModal={ () => showModal(false, null) } />
        }
        </>
    )
}
