import React from 'react';

import './skillLevel.css';

export const SkillLevel = ( {languaje} ) => {
    return (
        <div className="skillScale">
            <small>
                {(languaje === 'es') && <>La escala utilizada para medir el dominio de cada skill se basa en 5 niveles.</>}
                {(languaje === 'en') && <>The scale used to grade each skill is based in 5 levels.</>}
                {(languaje === 'pt') && <>A escala usada para medir o domínio de cada habilidade é baseada em 5 níveis.</>}  
            </small>
            <div className="skillLevelBar">
                <ol>
                    <li>{(languaje === 'es') && <>Novato</>}
                        {(languaje === 'en') && <>Newcomer</>}
                        {(languaje === 'pt') && <>Novato</>} 
                    </li>
                    <li>
                        {(languaje === 'es') && <>Básico</>}
                        {(languaje === 'en') && <>Basic</>}
                        {(languaje === 'pt') && <>Básico</>} 
                    </li>
                    <li>
                        {(languaje === 'es') && <>Intermedio</>}
                        {(languaje === 'en') && <>Middle</>}
                        {(languaje === 'pt') && <>Intermediário</>} 
                    </li>
                    <li>
                        {(languaje === 'es') && <>Avanzado</>}
                        {(languaje === 'en') && <>Advanced</>}
                        {(languaje === 'pt') && <>Avançado</>} 
                    </li>
                    <li>{(languaje === 'es') && <>Experto</>}
                        {(languaje === 'en') && <>Expert</>}
                        {(languaje === 'pt') && <>Especialista</>}  
                    </li>
                </ol>
            </div>
        </div>
    )
}
