import React, { useEffect, useState } from 'react';
import { useLoadImages } from '../../hooks/useLoadImages';

import './resume.css';
import dataEsp from '../../data/esp/resumeEsp.json';
import dataEng from '../../data/eng/resumeEng.json';
import { SkillLevel } from '../skillLevel/SkillLevel';

export const Resume = ( {languaje} ) => {
    const [currentData, setCurrentData] = useState(dataEsp);
    const [skillList, setSkillList] = useState([]);
    const [skillInfo, setSkillInfo] = useState(false);
    const [levelOrder, setLevelOrder] = useState('asc');
    const [nameOrder, setNameOrder] = useState('asc');

    const getLevel = (level) => {
        if(level === 1){
            return <span className={"badge level"+level}> 
                        {(languaje === 'es') && <>Novato</>}
                        {(languaje === 'en') && <>Newcomer</>}
                        {(languaje === 'pt') && <>Novato</>} 
                    </span>
        }
        else if(level === 2){
            return <span className={"badge level"+level}> 
                        {(languaje === 'es') && <>Básico</>}
                        {(languaje === 'en') && <>Basic</>}
                        {(languaje === 'pt') && <>Básico</>} 
                    </span>
        }
        else if(level === 3){
            return <span className={"badge level"+level}> 
                        {(languaje === 'es') && <>Intermedio</>}
                        {(languaje === 'en') && <>Middle</>}
                        {(languaje === 'pt') && <>Intermediário</>} 
                    </span>
        }
        else if(level === 4){
            return <span className={"badge level"+level}> 
                        {(languaje === 'es') && <>Avanzado</>}
                        {(languaje === 'en') && <>Advanced</>}
                        {(languaje === 'pt') && <>Avançado</>} 
                    </span>
        }
        else if(level === 5){
            return <span className={"badge level"+level}> 
                        {(languaje === 'es') && <>Experto</>}
                        {(languaje === 'en') && <>Expert</>}
                        {(languaje === 'pt') && <>Especialista</>}  
                    </span>
        }
    }
      
    const getAge = () => {
        const birth = new Date(1997, 8, 17);
        const today = new Date(Date.now());
        
        let ageDifMs = today - birth;
        let ageDate = new Date(ageDifMs);
        
        return Math.abs(ageDate.getUTCFullYear() - 1970);
    }
    
    const processText = (text) => {
        const age = getAge();
        text = text.replace('<age>', age.toString());
        
        return text;
    }

    const sortSkills = (type) => {
        let list = [...skillList];

        if(type === 'name' && nameOrder === 'dec'){
            setNameOrder('asc');
            list.sort((a, b) => {
                if (a.name > b.name) {
                    return 1;
                }
                if (a.name < b.name) {
                    return -1;
                }
                return 0;
            });
        }
        else if(type === 'name' && nameOrder === 'asc'){
            setNameOrder('dec');
            list.sort((a, b) => {
                if (a.name > b.name) {
                    return -1;
                }
                if (a.name < b.name) {
                    return 1;
                }
                return 0;
            });
        }
        else if(type === 'level' && levelOrder === 'dec'){
            setLevelOrder('asc');
            list.sort((a, b) => {
                if (a.level > b.level) {
                    return -1;
                }
                if (a.level < b.level) {
                    return 1;
                }
                return 0;
            });
        }
        else if(type === 'level' && levelOrder === 'asc'){
            setLevelOrder('dec');
            list.sort((a, b) => {
                if (a.level > b.level) {
                    return 1;
                }
                if (a.level < b.level) {
                    return -1;
                }
                return 0;
            });
        }

        setSkillList(list);
    }
    
    const selectLanguaje = (lang) => {
        (lang === "es") &&
            setCurrentData(dataEsp);
        (lang === "en") &&
            setCurrentData(dataEng);
    }

    const showSkillInfo = (val) => {
        setSkillInfo(val);
    }

    const images = useLoadImages( require.context('../../img/skills', false, /\.(png|jpe?g|svg)$/) );

    useEffect(() => {
        selectLanguaje(languaje);
        setSkillList(currentData.content[3].list.sort((a, b) => {
            if (a.level > b.level) {
                return -1;
            }
            if (a.level < b.level) {
                return 1;
            }
            return 0;
        }));
    }, [currentData, languaje]);

    return (
        <section className="resumeSection">
            <h2> {currentData.title} </h2>  
            {
                currentData.content.map((item) => {
                    if(item.type === 'paragraph'){
                        return <p key={ "resume" + item.type + item.id }>{ processText(item.info) }</p>
                    }
                    else if(item.type === 'listSkills'){
                        return (
                            <div className="skillContainer" key={ "resume" + item.type + item.id}>
                                <div className="order">
                                    <button onClick={() => {sortSkills("name")}}>
                                        {(languaje === 'es') && <>Nombre </>}
                                        {(languaje === 'en') && <>Name </>}
                                        {(languaje === 'pt') && <>Nome </>} 
                                        {(nameOrder === 'asc') ? <>(A - Z)</> : <>(Z - A)</>} 
                                    </button>
                                    <button onClick={() => {sortSkills("level")}}>
                                        {(languaje === 'es') && <>Nivel </>}
                                        {(languaje === 'en') && <>Level </>}
                                        {(languaje === 'pt') && <>Nível </>}
                                        {(levelOrder === 'asc') ? <>(5 - 1)</> : <>(1 - 5)</>} 
                                    </button>
                                    <button className="infoBtn" onClick={() => {showSkillInfo(true)}} onMouseOver={() => {showSkillInfo(true)}} onMouseOut={() => {showSkillInfo(false)}}><i className="fas fa-info-circle"></i></button>
                                </div>
                                {
                                    (skillInfo) &&
                                        <SkillLevel languaje={languaje} />
                                }
                                
                                { skillList.map((skill) => {
                                    return (
                                        <div className={"skill level"+ skill.level} key={"skill"+skill.id}>
                                            <div className="skillBox"> 
                                                <img src={images[`${skill.imgName}.png`].default} alt={skill.name}></img>
                                                <span className="title">{skill.name}</span>
                                                {getLevel(skill.level)}
                                            </div>
                                        </div>
                                    )
                                })
                                }
                            </div>
                        )
                        
                    }
                    return <></>
                })
            }
        </section>
    )
}
