/**
 * @function useLoadImages create an object with all the images contained in a directory
 * @param {Object} context is a require.context() object
 * @returns Object with the images, the source is object[key].default
 */
export const useLoadImages = (context) => {
    let images = {};
    context.keys().forEach((item) => {
        images[item.replace('./', '')] = context(item);
    });

    return images;
};